html {
  height: 100%; }

body {
  font: 300 14px/18px 'Droid Sans', sans-serif;
  height: 100%;
  color: #000; }

a {
  text-decoration: underline;
  color: #006; }
  a:hover {
    color: #f79d15; }

.wrapper {
  width: 100%;
  min-width: 320px;
  height: auto !important;
  height: 100%;
  min-height: 100%;
  margin-bottom: -40px;
  background: linear-gradient(to bottom, #fff 0%, #d9d9d9 100%);
  background-color: #e9e9e9; }

.container {
  overflow: hidden;
  width: 560px;
  margin: 0 auto;
  padding: 0 10px; }

.strong {
  font-weight: 600; }

.gNotify {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; }
  .gNotify > p {
    display: none;
    padding: 24px 12px;
    color: #fff;
    background-color: #f00; }

.noJs-notify, .qty-notify {
  margin: 0; }

.noJs .gNotify .noJs-notify {
  display: block; }

.btn {
  font: 600 24px/1 'Droid Sans', sans-serif;
  display: block;
  padding: 12px 10px;
  text-decoration: none;
  color: #fff;
  border-radius: 10px;
  background-color: #006;
  box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.5); }
  .btn:hover {
    color: #fff;
    background-color: #33f; }
  .btn:active {
    position: relative;
    top: 1px;
    left: 1px; }

/* Header -----------------------------------------------------------------------------*/
.header {
  padding: 130px 0 24px; }

.header-logo {
  float: left;
  padding: 0 20px; }

.content {
  padding: 0 0 64px; }

.bskt {
  font-family: 'Droid Serif', serif; }

.bskt-heading {
  padding: 36px 20px 0;
  margin: 0 0 12px;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 600; }

.bskt-descr {
  margin: 0;
  padding: 0 20px; }

.bsqt-empty-message {
  display: none; }

.bskt-form {
  padding: 60px 0;
  font-size: 18px; }
  .bskt-form.empty .bsqt-empty-message {
    display: block;
    padding: 15px 20px; }

.bskt-form-head, .bskt-form-row {
  overflow: hidden; }

.bskt-form-head {
  font-weight: bold;
  margin: 0 0 10px;
  border-bottom: 1px solid #000; }
  .bskt-form-head .bskt-col {
    height: 20px;
    line-height: 20px;
    padding-top: 0;
    padding-bottom: 0; }
    .bskt-form-head .bskt-col.bskt-col-qty {
      text-align: center; }
    .bskt-form-head .bskt-col.last {
      border-bottom: none; }

.bskt-form-values .bskt-form-row:nth-child(even), .bskt-form-values .bskt-form-row.even {
  background-color: #ddd; }

.bskt-range {
  float: right;
  width: 16px;
  margin: 0 0 0 2px; }

.bskt-range-up, .bskt-range-down {
  cursor: pointer;
  height: 14px;
  display: block;
  background-color: #999;
  text-align: center;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  line-height: 14px;
  user-select: none; }
  .bskt-range-up:hover, .bskt-range-down:hover {
    background-color: #666; }
  .bskt-range-up:active, .bskt-range-down:active {
    position: relative;
    top: 1px;
    left: 1px; }

.bskt-range-up {
  margin: 0 0 2px; }

.bskt-input {
  height: 28px;
  border: 1px solid #999;
  outline: 0 none;
  width: 30px;
  text-align: right;
  padding: 0 5px; }
  .bskt-input:hover, .bskt-input:focus, .bskt-input:active {
    border-color: #666; }

.bskt-del {
  cursor: pointer;
  float: right;
  width: 30px;
  height: 30px;
  overflow: hidden;
  text-indent: 50000px;
  background: transparent url('../gfx/ico/remove-ico.png') no-repeat scroll -1px -1px; }
  .bskt-del:hover {
    background-position: -1px -33px; }
  .bskt-del:active {
    position: relative;
    top: 1px;
    left: 1px; }

.bskt-form-subRows {
  color: #666;
  padding: 36px 0 20px; }
  .bskt-form-subRows .bskt-col {
    padding-top: 0;
    padding-bottom: 0; }

.bskt-col {
  padding: 10px 20px 10px 0;
  float: left;
  line-height: 30px; }
  .bskt-col.last {
    width: 30px;
    padding-right: 0; }

.bskt-col-val {
  width: 70px;
  text-align: right;
  white-space: nowrap; }

.bskt-col-name {
  width: 210px;
  padding: 10px 20px 10px 20px; }

.bskt-subm {
  overflow: hidden;
  padding: 20px;
  margin-right: 38px; }
  .bskt-subm .btn {
    float: right;
    margin: 0 0 0 10px; }

/* Footer
-----------------------------------------------------------------------------*/
.footer {
  position: relative;
  min-width: 320px;
  height: 40px;
  margin: -41px auto 0;
  color: #fff;
  border-top: 1px solid #000;
  background: #666; }

.footer-cr {
  line-height: 40px;
  font-size: 12px;
  margin: 0;
  padding: 0 20px; }

@media screen and (max-width: 560px) {
  .header {
    padding: 20px 0 10px; }
  .bskt-heading {
    padding-top: 0; }
  .bskt-form {
    padding-top: 20px; }
  .container {
    width: 100%;
    margin: 0 auto;
    padding: 0; }
  .bskt-col {
    width: 15%;
    padding-right: 10px; }
  .bskt-col-name {
    width: 32%;
    padding-right: 10px; } }

@media screen and (max-width: 460px) {
  .bskt-form-head {
    display: none; }
  .bskt-form {
    padding-top: 30px; }
  .bskt-form-values .bskt-col {
    width: 19%; }
  .bskt-form-values .bskt-col-price {
    padding-left: 20px;
    text-align: left; }
  .bskt-form-row {
    margin-bottom: 10px; }
    .bskt-form-row .bskt-col-name {
      width: 100%; }
  .bskt-form-subRows .bskt-col-val, .totalRow .bskt-col-val {
    width: auto; }
  .bskt-form-subRows .bskt-col-name, .totalRow .bskt-col-name {
    width: 55%; }
  .bskt-form-subRows .bskt-col-price, .totalRow .bskt-col-price {
    width: 20%; }
  .bskt-subm {
    margin-right: 0; } }

